import React from 'react';

const User = (props) => {
    let user = props.user.login;
    return (
        <div className="user_container">
            <div className="avatar">
                <img alt="avatar" src="/images/avatar2.gif"/>
            </div>
            <div className="nfo">

                <div><span>Email:</span> {user.email}</div>
            </div>
        </div>
    );
};



export default User;



